import type { Case, CaseStatus } from '@/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { 
  FileText, Gavel, CheckCircle2, AlertCircle, CalendarDays, Scale, User, Users, Landmark, Search,
  Clock, Activity, PauseCircle, ScrollText
} from 'lucide-react';
import Link from 'next/link';

interface CaseCardProps {
  caseData: Case;
}

const statusIcons: Record<CaseStatus, React.ElementType> = {
  Pending: Clock,
  Filed: FileText,
  Investigation: Search,
  'In Progress': Activity,
  Hearing: Gavel,
  Judgement: ScrollText, // Changed from Gavel for variety
  Resolved: CheckCircle2,
  Appealed: AlertCircle,
  'On Hold': PauseCircle,
  Closed: CheckCircle2,
};

const statusColors: Record<CaseStatus, string> = {
  Pending: 'bg-gray-500/20 text-gray-700 border-gray-500/30',
  Filed: 'bg-blue-500/20 text-blue-700 border-blue-500/30',
  Investigation: 'bg-yellow-500/20 text-yellow-700 border-yellow-500/30',
  'In Progress': 'bg-cyan-500/20 text-cyan-700 border-cyan-500/30',
  Hearing: 'bg-purple-500/20 text-purple-700 border-purple-500/30',
  Judgement: 'bg-indigo-500/20 text-indigo-700 border-indigo-500/30',
  Resolved: 'bg-green-500/20 text-green-700 border-green-500/30',
  Appealed: 'bg-orange-500/20 text-orange-700 border-orange-500/30',
  'On Hold': 'bg-pink-500/20 text-pink-700 border-pink-500/30',
  Closed: 'bg-green-700/30 text-green-800 border-green-700/40', // Slightly darker for closed
};


export default function CaseCard({ caseData }: CaseCardProps) {
  const StatusIcon = statusIcons[caseData.status] || AlertCircle;
  const statusColorClass = statusColors[caseData.status] || 'bg-gray-500/20 text-gray-700 border-gray-500/30';

  const formatDate = (dateString?: string) => {
    if (!dateString) return 'N/A';
    try {
      return new Date(dateString).toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' });
    } catch (e) {
      return dateString; // fallback to original string if date is invalid
    }
  };

  return (
    <Card className="w-full shadow-lg hover:shadow-xl transition-shadow duration-300 flex flex-col">
      <CardHeader>
        <div className="flex justify-between items-start">
          <CardTitle className="text-xl font-semibold text-primary">{caseData.title}</CardTitle>
          <Badge variant="outline" className={`text-sm px-3 py-1 ${statusColorClass}`}>
            <StatusIcon className="mr-2 h-4 w-4" />
            {caseData.status}
          </Badge>
        </div>
        <CardDescription className="text-sm text-muted-foreground">Case Number: {caseData.caseNumber}</CardDescription>
      </CardHeader>
      <CardContent className="space-y-3 flex-grow">
        <p className="text-sm leading-relaxed line-clamp-3">{caseData.description}</p>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-3 text-sm">
          <div className="flex items-center">
            <Users className="mr-2 h-4 w-4 text-muted-foreground" />
            <strong>Plaintiff:</strong><span className="ml-1 truncate" title={caseData.plaintiff}>{caseData.plaintiff}</span>
          </div>
          <div className="flex items-center">
            <Users className="mr-2 h-4 w-4 text-muted-foreground" />
            <strong>Defendant:</strong><span className="ml-1 truncate" title={caseData.defendant}>{caseData.defendant}</span>
          </div>
          <div className="flex items-center">
            <Landmark className="mr-2 h-4 w-4 text-muted-foreground" />
            <strong>Court:</strong><span className="ml-1 truncate" title={caseData.court}>{caseData.court}</span>
          </div>
          {caseData.judge && (
            <div className="flex items-center">
              <Scale className="mr-2 h-4 w-4 text-muted-foreground" />
              <strong>Judge:</strong><span className="ml-1 truncate" title={caseData.judge}>{caseData.judge}</span>
            </div>
          )}
          <div className="flex items-center">
            <CalendarDays className="mr-2 h-4 w-4 text-muted-foreground" />
            <strong>Filing Date:</strong><span className="ml-1">{formatDate(caseData.filingDate)}</span>
          </div>
          <div className="flex items-center">
            <CalendarDays className="mr-2 h-4 w-4 text-muted-foreground" />
            <strong>Last Update:</strong><span className="ml-1">{formatDate(caseData.lastUpdate)}</span>
          </div>
          {caseData.nextHearingDate && (
             <div className="flex items-center text-accent-foreground bg-accent/20 p-2 rounded-md md:col-span-2">
              <CalendarDays className="mr-2 h-4 w-4 text-accent" />
              <strong>Next Hearing:</strong><span className="ml-1 font-semibold">{formatDate(caseData.nextHearingDate)}</span>
            </div>
          )}
        </div>
      </CardContent>
      <CardFooter>
        <Link href={`/cases/${caseData.id}`} passHref legacyBehavior>
          <Button variant="outline" className="w-full md:w-auto">
            View Details
            <FileText className="ml-2 h-4 w-4" />
          </Button>
        </Link>
      </CardFooter>
    </Card>
  );
}
