'use client';

import { useState, useEffect, useMemo } from 'react';
import CaseSearch from '@/components/cases/CaseSearch';
import CaseCard from '@/components/cases/CaseCard';
import type { Case } from '@/types';
import { mockCases } from '@/data/mockData'; 
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { AlertTriangle, ListFilter, Loader2, PlusCircle, SearchX, LayoutDashboard } from 'lucide-react';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"


export default function DashboardPage() {
  const { user, loading: authLoading } = useAuth();
  const [allCases, setAllCases] = useState<Case[]>([]);
  const [filteredCases, setFilteredCases] = useState<Case[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setAllCases(mockCases);
    setFilteredCases(mockCases);
    setIsLoading(false);
  }, []);

  useEffect(() => {
    let currentCases = [...allCases];

    if (user?.role === 'Judge') {
      // If user is a judge, filter by judgeId first
      currentCases = currentCases.filter(c => c.judgeId === user.id);
    }


    if (statusFilter !== 'all') {
      currentCases = currentCases.filter(c => c.status === statusFilter);
    }

    if (searchTerm) {
      const lowerSearchTerm = searchTerm.toLowerCase();
      currentCases = currentCases.filter(c =>
        c.caseNumber.toLowerCase().includes(lowerSearchTerm) ||
        c.title.toLowerCase().includes(lowerSearchTerm) ||
        (c.plaintiff && c.plaintiff.toLowerCase().includes(lowerSearchTerm)) ||
        (c.defendant && c.defendant.toLowerCase().includes(lowerSearchTerm))
      );
    }
    setFilteredCases(currentCases);
  }, [searchTerm, statusFilter, allCases, user]);

  const handleSearch = (term: string) => {
    setSearchTerm(term);
  };
  
  const uniqueStatuses = useMemo(() => {
    const casesToConsider = user?.role === 'Judge' ? allCases.filter(c => c.judgeId === user.id) : allCases;
    const statuses = new Set(casesToConsider.map(c => c.status));
    return ['all', ...Array.from(statuses).sort()];
  }, [allCases, user]);


  if (authLoading || isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-lg text-muted-foreground">Loading dashboard...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="text-center py-10">
        <AlertTriangle className="mx-auto h-12 w-12 text-destructive mb-4" />
        <h1 className="text-2xl font-semibold mb-2">Access Denied</h1>
        <p className="text-muted-foreground mb-6">Please log in to view the dashboard and track cases.</p>
        <Link href="/login">
          <Button>Login</Button>
        </Link>
      </div>
    );
  }
  
  const dashboardTitle = user.role === 'Judge' ? 'My Allocated Cases' : 'Case Dashboard';

  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <div className="flex flex-col md:flex-row justify-between items-center mb-8 gap-4">
        <h1 className="text-3xl font-bold tracking-tight flex items-center">
          <LayoutDashboard className="mr-3 h-8 w-8 text-primary" />
          {dashboardTitle}
        </h1>
        {(user.role === 'CourtOfficial' || user.role === 'Judge') && (
           <Link href="/cases/new">
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" /> Add New Case
            </Button>
          </Link>
        )}
      </div>
      
      <div className="flex flex-col md:flex-row gap-4 mb-6 p-4 bg-muted/50 rounded-lg border">
        <CaseSearch onSearch={handleSearch} initialSearchTerm={searchTerm} />
        <div className="flex items-center gap-2">
          <ListFilter className="h-5 w-5 text-muted-foreground" />
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-full md:w-[200px] bg-background">
              <SelectValue placeholder="Filter by status" />
            </SelectTrigger>
            <SelectContent>
              {uniqueStatuses.map(status => (
                <SelectItem key={status} value={status}>
                  {status === 'all' ? 'All Statuses' : status}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      {filteredCases.length > 0 ? (
        <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
          {filteredCases.map((caseItem) => (
            <CaseCard key={caseItem.id} caseData={caseItem} />
          ))}
        </div>
      ) : (
        <div className="text-center py-16 bg-card rounded-lg border-2 border-dashed">
          <SearchX className="mx-auto h-20 w-20 text-muted-foreground mb-6" />
          <h2 className="text-2xl font-semibold text-foreground">No Cases Found</h2>
          <p className="text-muted-foreground mt-2 max-w-md mx-auto">
            {searchTerm || statusFilter !== 'all' 
              ? "Your search or filter criteria did not match any cases." 
              : (user.role === 'Judge' ? "You currently have no cases allocated to you, or there are no cases matching the active filters." : "There are currently no cases to display.")}
          </p>
           { (user.role === 'CourtOfficial' || user.role === 'Judge') && (
             <Link href="/cases/new" className="mt-6 inline-block">
                <Button variant="outline">
                  <PlusCircle className="mr-2 h-4 w-4" /> Add Your First Case
                </Button>
            </Link>
           )}
        </div>
      )}
    </div>
  );
}
