'use client';

import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Search } from "lucide-react"
import type { ChangeEvent, FormEvent } from "react";

interface CaseSearchProps {
  onSearch: (searchTerm: string) => void;
  initialSearchTerm?: string;
}

export default function CaseSearch({ onSearch, initialSearchTerm = '' }: CaseSearchProps) {
  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData(event.currentTarget);
    const searchTerm = formData.get('search') as string;
    onSearch(searchTerm);
  };

  return (
    <form onSubmit={handleSubmit} className="flex w-full max-w-2xl items-center space-x-2 mb-8">
      <Input
        type="search"
        name="search"
        placeholder="Search by case number, title, plaintiff, defendant..."
        className="flex-grow"
        defaultValue={initialSearchTerm}
      />
      <Button type="submit" variant="outline" size="icon" aria-label="Search cases">
        <Search className="h-5 w-5" />
      </Button>
    </form>
  )
}
